.header {
    width: 100%;
    height: 518px;

    &-main {
        position: relative;
        width: 100%;
        height: 100%;
        background: url('/img/promo.jpg') center no-repeat ;

        &__img {
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.34);
        }

        &__btn {
            position: absolute;
            bottom: 25.38px;
            width: 254.81px;
            height: 60.62px;
            background: $main-color;
            color: #FFFFFF;
            font-family: $ff;
            font-size: 28px;
            border: 1px solid #EBF1FD;
            cursor: pointer;
        }
    }
    &-container {
        position: absolute;
        display: flex;
        align-items: center;
        height: 136px;
        top: 62px;
        border-left: 2px solid #FFFFFF;

        &__name {
            display: flex;
            flex-direction: column;
            margin-left: 29px;

            &-title {
                font-family: $ff;
                font-weight: 400;
                font-size: 64px;
                color: #FFFFFF;
                letter-spacing: $lc;
            }
    
            &-subtitle {
                margin-top: 10px;
                font-family: $ff;
                color: #FFFFFF;
                font-size: 28px;
                letter-spacing: $lc;
            }
        }
    }
}